.splash-screen {
  background: #fefefe;
  height: 100%; }
  .splash-screen__hero {
    height: 60%; }
  .splash-screen__img {
    width: 125px; }
  .splash-screen__title {
    font-size: 26px;
    color: #77B02A; }
  .splash-screen__subtitle {
    font-size: 18px;
    color: #838383;
    font-weight: 400; }
  .splash-screen__cta-container {
    background: #77B02A;
    height: 40%;
    z-index: 1;
    position: relative; }
    .splash-screen__cta-container:before, .splash-screen__cta-container:after {
      top: 0;
      left: -25%;
      z-index: -1;
      width: 150%;
      height: 75%;
      background: inherit;
      -webkit-transform: rotate(-2deg);
              transform: rotate(-2deg);
      -webkit-transform-origin: 0 0;
              transform-origin: 0 0; }
    .splash-screen__cta-container:before {
      height: 30%;
      background: #77B02A;
      -webkit-transform: rotate(-10deg);
              transform: rotate(-10deg);
      -webkit-transform-origin: 3% 0;
              transform-origin: 3% 0; }
      @media (min-width: 610px) {
        .splash-screen__cta-container:before {
          display: none; } }
  .splash-screen__cta {
    padding: 1rem !important;
    border-radius: 0 !important; }

.has-dd::before,
.has-dd::after {
  position: absolute;
  content: '';
  pointer-events: none; }
